  --title-size: calc(28px + (60 - 28) * (100vw - 375px) / (1920 - 375));
  --tel-size: calc(20px + (24 - 20) * (100vw - 375px) / (1920 - 375));
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 10;
}

.header__inner {
  min-height: 88px;
  align-items: center;
  flex-wrap: wrap;
}

.header__logo {
  margin-right: calc(10px + (114 - 10) * (100vw - 1200px) / (1920 - 1200));
}

.header__logo.active {
  display: none;
}

.header__phone {
  margin-left: auto;
}

.header__social-mobile.social {
  display: none;
}

.phone {
  display: flex;
  align-items: center;
}

.phone__icon {
  width: 22px;
  height: 19px;
}

.phone__link {
  display: block;
  padding-left: 14px;
  font-size: var(--tel-size);
  line-height: 100%;
  color: var(--color-tel);
}

.phone.active {
  display: block;
  z-index: 5;
}

.phone.active .phone__link {
  color: var(--color-white);
}

.burger {
  display: none;
}

.hero {
  position: relative;
  padding-top: 285px;
  padding-bottom: 60px;
  min-height: 100vh;
  background-image: url("../../img/hero/hero-bg.webp");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.hero__title {
  max-width: 740px;
  margin-bottom: 56px;
}

.hero__descr {
  margin-bottom: 63px;
  max-width: 360px;
  font-size: 24px;
  line-height: 158%;
}

.hero__btn-order {
  margin-bottom: 195px;
}

.social {
  display: flex;
  align-items: flex-end;
}

.social__link {
  display: block;
  width: 25px;
  height: 25px;
  transform-origin: center center;
  background-repeat: no-repeat;
  transform-origin: center center;
  transition: all ease-out 240ms;
}

.social__link:hover {
  transform: scale(1.2);
}

.social__link:focus {
  outline: transparent;
}

.social__link:focus-visible {
  transform: scale(1.2) translateY(-5%);
}

.social__link:active {
  transform: scale(.95);
}

.social__link:not(:last-child) {
  margin-right: 25px;
}

.social__link svg {
  width: 25px;
  height: 25px;
  fill: rgba(0,0,0,0.6);
}

.scroll__link {
  position: absolute;
  display: block;
  bottom: 57px;
  left: 50%;
  transform: translateX(-50%);
  width: 1px;
  height: 98px;
  background-color: rgba(0,0,0,0.4);
}

.scroll__link::after {
  content: '';
  position: absolute;
  bottom: 75px;
  width: 1px;
  height: 22.72px;
  background-color: var(--color-black);
  animation-name: line;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

@keyframes line {
  0% {
    bottom: 75px;
  }

  100% {
    bottom: 0;
  }
}

.menu {
  position: relative;
}

.menu.active {
  top: 0;
}

.menu__item:not(:last-child) {
  margin-right: calc(10px + (92 - 10) * (100vw - 1200px) / (1920 - 1200));
}

.menu__link {
  position: relative;
  padding: 5px;
  padding-bottom: 6px;
  font-size: 20px;
  line-height: 100%;
  letter-spacing: 0.02em;
  color: var(--color-black);
}

.menu__link.active {
  border-bottom: 3px solid var(--color-green);
}

.menu__link:after {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  display: block;
  width: 0;
  height: 3px;
  background-color: var(--color-green);
  transition: width 0.3s ease-out;
}

.menu__link:hover::after,
.menu__link:focus:after {
  width: 100%;
}

@media (min-width: 769px) {
  .menu__list {
    display: flex;
    align-items: center;
  }
}

@media (max-width: 1200px) {
  .header__inner {
    padding-top: 34px;
  }

  .header__menu {
    order: 2;
    margin: 15px auto 0;
  }

  .menu__item:not(:last-child) {
    margin-right: 80px;
  }
}

@media (max-width: 1100px) {
  .menu__item:not(:last-child) {
    margin-right: 40px;
  }
}

@media (max-width: 991px) {
  .menu__item:not(:last-child) {
    margin-right: 20px;
  }
}

@media (max-width: 810px) {
  .menu__item:not(:last-child) {
    margin-right: 10px;
  }
}

@media (max-width: 769px) {
  .header__phone {
    margin: 0;
  }

  .header__social-mobile.active {
    display: block;
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
  }

  .header__social-mobile.active svg {
    fill: var(--color-white);
  }

  .phone__icon {
    display: none;
  }

  .menu {
    margin: 0;
    position: fixed;
    left: 0;
    top: -100%;
    width: 100%;
    height: 100%;
    background-color: #0F424B;
    overflow: auto;
    transition: top 0.5s ease 0s;
  }

  .menu__list {
    text-align: center;
    padding-top: 140px;
    padding-bottom: 75px;
  }

  .menu__item:not(:last-child) {
    margin-bottom: 50px;
  }

  .menu__item {
    flex-wrap: wrap;
  }

  .menu__link {
    color: var(--color-white);
  }
}

@media (max-width: 768px) {
  .header__inner {
    padding-top: 0;
  }

  .header__inner {
    justify-content: space-between;
  }

  .phone {
    display: none;
  }

  .burger {
    display: block;
    position: relative;
    width: 28px;
    height: 14px;
    cursor: pointer;
    background-color: transparent;
    border: none;
    z-index: 5;
  }

  .burger::before,
  .burger::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 2px;
    background-color: var(--color-text);
  }

  .burger::after {
    top: auto;
    bottom: 0;
  }

  .burger span {
    display: inline-block;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: 2px;
    background-color: var(--color-text);
  }

  .burger.active span {
    transform: scale(0) translate(0, -50%);
  }

  .burger.active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0, -50%);
    background-color: var(--color-green);
  }

  .burger.active::after {
    bottom: 50%;
    transform: rotate(45deg) translate(0, 50%);
    background-color: var(--color-green);
  }
}